import { auth } from "@/auth"
import { prisma } from "@/lib/prisma"
import { getTemplateGroups } from "@/actions/template-actions"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion"
import { Plus, LayoutTemplate, Copy } from "lucide-react"
import { AddTaskForm } from "@/components/templates/add-task-form"
import { CreateGroupForm } from "@/components/templates/create-group-form"

// import { getProjects } from "@/actions/project-actions" // Not used directly, using Prisma
import { ApplyTemplateDialog } from "@/components/templates/apply-template-dialog"

export default async function TemplatesPage() {
    const session = await auth()
    if (!session?.user) return null

    const groups = await getTemplateGroups()
    // Need jobs for the dialog
    const projectsData = await prisma.project.findMany({
        orderBy: { name: "asc" }
    })

    // Serialize dates for client component
    const projects = projectsData.map(p => ({
        id: p.id,
        name: p.name
    }))

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Templates</h1>
                    <p className="text-muted-foreground">
                        Manage task groups for standard operating procedures.
                    </p>
                </div>
            </div>

            <div className="grid gap-6 md:grid-cols-3">
                <div className="md:col-span-2 space-y-6">
                    <Accordion type="single" collapsible className="w-full">
                        {groups.map((group) => (
                            <AccordionItem key={group.id} value={group.id}>
                                <AccordionTrigger className="hover:no-underline">
                                    <div className="flex items-center gap-4 text-left w-full pr-4">
                                        <div className="h-10 w-10 rounded bg-primary/10 flex items-center justify-center text-primary">
                                            <LayoutTemplate className="h-5 w-5" />
                                        </div>
                                        <div>
                                            <div className="font-semibold">{group.name}</div>
                                            <div className="text-xs text-muted-foreground font-normal">{group.templates.length} tasks defined</div>
                                        </div>
                                    </div>
                                </AccordionTrigger>
                                <AccordionContent className="p-4 bg-muted/30 rounded-lg space-y-4">
                                    {/* Action Header in Content */}
                                    <div className="flex justify-end border-b pb-4 mb-4">
                                        <ApplyTemplateDialog group={group} projects={projects} />
                                    </div>
                                    <div className="relative border-l-2 border-muted ml-4 space-y-6 pb-2">
                                        {group.templates
                                            .sort((a, b) => (a.relativeDueDays || 0) - (b.relativeDueDays || 0))
                                            .map((t) => (
                                                <div key={t.id} className="relative pl-6">
                                                    <div className="absolute -left-[9px] top-1 h-4 w-4 rounded-full bg-background border-2 border-primary" />
                                                    <div className="flex items-center justify-between bg-card p-3 rounded border shadow-sm">
                                                        <div className="flex flex-col gap-1">
                                                            <span className="font-medium text-sm">{t.title}</span>
                                                            <span className="text-xs text-muted-foreground flex items-center gap-1">
                                                                <span className="font-mono font-bold text-primary">
                                                                    T+{t.relativeDueDays || 0}
                                                                </span>
                                                                days from start
                                                            </span>
                                                        </div>
                                                        <div className="flex items-center gap-2">
                                                            <span className={`text-[10px] px-1.5 py-0.5 rounded border font-mono ${t.priority === 'P0' ? 'bg-red-50 text-red-700 border-red-200' :
                                                                'bg-slate-50 text-slate-600 border-slate-200'
                                                                }`}>
                                                                {t.priority}
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            ))}
                                    </div>
                                    <div className="pt-4 border-t">
                                        <h4 className="text-sm font-medium mb-3">Add Task to Template</h4>
                                        <AddTaskForm groupId={group.id} />
                                    </div>
                                </AccordionContent>
                            </AccordionItem>
                        ))}
                    </Accordion>

                    {groups.length === 0 && (
                        <div className="text-center py-12 text-muted-foreground bg-muted/20 rounded-lg border-dashed border-2">
                            <Copy className="h-10 w-10 mx-auto mb-3 opacity-20" />
                            <p>No templates created yet.</p>
                        </div>
                    )}
                </div>

                <div>
                    <Card>
                        <CardHeader>
                            <CardTitle>New Template Group</CardTitle>
                            <CardDescription>Create a collection of tasks.</CardDescription>
                        </CardHeader>
                        <CardContent>
                            <CreateGroupForm />
                        </CardContent>
                    </Card>
                </div>
            </div>
        </div >
    )
}
